export function generatePalette(): void {
    let palette: SymbolPalette = new SymbolPalette({
        expandMode: 'Multiple',
        palettes: [
            //{ id: 'scratchpad', expanded: true, symbols: [], iconCss: 'ej-icon-New scratch-pad', title: 'Scratchpad' },
            { id: 'flow', expanded: true, symbols: getFlowShapes(), title: 'Flow Shapes' },

            { id: 'basic', expanded: false, symbols: getBasicShapes(), title: 'Basic Shapes' },
            
            { id: 'bpmn', expanded: false, symbols: getBPMNShapes(), title: 'BPMN Shapes' },

            { id: 'connectors', expanded: false, symbols: getConnectors(), title: 'Connectors' },
            
            { id: 'networkShapes', expanded: false, symbols: getNetworkShapes(), title: 'Network Shapes' },

           { id: 'FloorPlans', expanded: false, symbols: getFloorPlans(), title: 'Floor Plan Shapes' },

            { id: 'EleAudioShape', expanded: false, symbols: getElectricalAudioShapes(), title: 'Eletrical/Audio Shapes' },
            { id: 'EleCapcitorsShape', expanded: false, symbols: getElectricalCapcitorsShapes(), title: 'Eletrical/Capcitor Shapes' },
            { id: 'EleCapcitorsShape', expanded: false, symbols: getElectricalDiodesShapes(), title: 'Eletrical/Diodes Shapes' },
            { id: 'EleGatesShape', expanded: false, symbols: getElectricalGatesShapes(), title: 'Eletrical/Gates Shapes' },
            { id: 'EleInductorsShapes', expanded: false, symbols: getElectricalInductorsShapes(), title: 'Eletrical/Inductors Shapes' },
            { id: 'EleInstrumentsShapes', expanded: false, symbols: getElectricalInstrumentsShapes(), title: 'Eletrical/Instruments Shapes' },
            { id: 'EleMiscShapes', expanded: false, symbols: getElectricalMiscShapes(), title: 'Eletrical/Misc Shapes' },
            { id: 'EleOpticalShapes', expanded: false, symbols: getElectricalOpticalShapes(), title: 'Eletrical/Optical Shapes' },
            { id: 'EleLadderShape', expanded: false, symbols: getElectricalLadderShapes(), title: 'Eletrical/Ladder Shapes' },
            { id: 'EleResistorShape', expanded: false, symbols: getElectricalResistorShapes(), title: 'Eletrical/Resistor Shapes' },
            { id: 'EleEquipmentShapes', expanded: false, symbols: getElectricalEquipmentShapes(), title: 'Eletrical/Equipment Shapes' },
            { id: 'EleSourcesShapes', expanded: false, symbols: getElectricalSourcesShapes(), title: 'Eletrical/Source Shapes' },

            { id: 'EleSwitchesShapes', expanded: false, symbols: getElectricalSwitchesShapes(), title: 'Eletrical/Switches Shapes' },
            { id: 'EleTransmissionShape', expanded: false, symbols: getElectricalTransmissionShapes(), title: 'Eletrical/Transmission Shapes' },
            { id: 'EleTransistorsShapes', expanded: false, symbols: getElectricalTransistorsShapes(), title: 'Eletrical/Transistor Shapes' },
            { id: 'ElelVaccumShapes', expanded: false, symbols: getElectricalVaccumShapes(), title: 'Eletrical/Vaccum Shapes' },
            { id: 'EleWaveformsShapes', expanded: false, symbols: getElectricalWaveformsShapes(), title: 'Eletrical/WaveForms Shapes' }
            
            
        ],
        width: '100%', height: '100%', symbolHeight: 50, symbolWidth: 50,
        symbolPreview: { height: 100, width: 100 },
        enableSearch: true,
        getNodeDefaults: setPaletteNodeDefaults,
        symbolMargin: { left: 12, right: 12, top: 12, bottom: 12 },
        getSymbolInfo: (symbol: NodeModel): SymbolInfo => {
            return { fit: true };
        }
    });
    palette.appendTo('#symbolpalette');
}

function setPaletteNodeDefaults(node: NodeModel): void {
    if (node.id === 'Terminator' || node.id === 'Process') {
        node.width = 130;
        node.height = 65;
    }  else {
        node.width = 50;
        node.height = 50;
    }
    node.ports = [
        { offset: { x: 0, y: 0.5 }, visibility: PortVisibility.Connect | PortVisibility.Hover, constraints: PortConstraints.Draw },
        { offset: { x: 0.5, y: 0 }, visibility: PortVisibility.Connect | PortVisibility.Hover, constraints: PortConstraints.Draw },
        { offset: { x: 1, y: 0.5 }, visibility: PortVisibility.Connect | PortVisibility.Hover, constraints: PortConstraints.Draw },
        { offset: { x: 0.5, y: 1 }, visibility: PortVisibility.Connect | PortVisibility.Hover, constraints: PortConstraints.Draw },
       
    ];
    

    node.style.strokeColor = '#3A3A3A';
}